import {
  Box,
  Button,
  Card,
  CardHeader,
  colors,
  Divider,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableRow,
  Typography
} from '@material-ui/core';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import clsx from 'clsx';
import numeral from 'numeral';
import PropTypes from 'prop-types';
import React, { FC, useCallback, useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Link as RouterLink } from 'react-router-dom';
import GenericMoreButton from 'src/components/GenericMoreButton';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { Theme } from 'src/theme';
import { Product } from 'src/types/reports';
import axios from 'src/utils/axios';
import CircularProgress from './CircularProgress';

interface MostProfitableProductsProps {
  className?: string;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {},
  image: {
    flexShrink: 0,
    height: 56,
    width: 56
  },
  subscriptions: {
    fontWeight: theme.typography.fontWeightMedium
  },
  value: {
    color: colors.green[600],
    fontWeight: theme.typography.fontWeightMedium
  }
}));

const MostProfitableProducts: FC<MostProfitableProductsProps> = ({
  className,
  ...rest
}) => {
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [products, setProducts] = useState<Product[]>([]);

  const getProducts = useCallback(async () => {
    try {
      const response = await axios.get<{ products: Product[] }>(
        '/api/reports/profitable-products'
      );

      if (isMountedRef.current) {
        setProducts(response.data.products);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getProducts();
  }, [getProducts]);

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <CardHeader
        action={<GenericMoreButton />}
        title="Most Profitable Products"
      />
      <Divider />
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableBody>
              {products.map(product => (
                <TableRow hover key={product.id}>
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <img
                        alt="Product"
                        className={classes.image}
                        src={product.image}
                      />
                      <Box ml={2}>
                        <Typography variant="h6" color="textPrimary">
                          {product.name}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          <span className={classes.subscriptions}>
                            {numeral(product.subscriptions).format('0,0')}
                          </span>{' '}
                          Active
                        </Typography>
                      </Box>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Typography variant="h6" color="textPrimary">
                      Price
                    </Typography>
                    <Typography noWrap variant="body2" color="textSecondary">
                      <span className={classes.value}>
                        {numeral(product.price).format(
                          `${product.currency}0,0.00`
                        )}
                      </span>{' '}
                      monthly
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Box
                      display="flex"
                      alignItems="center"
                      justifyContent="flex-end"
                    >
                      <Box mr={2}>
                        <Typography
                          align="right"
                          variant="h6"
                          color="textPrimary"
                        >
                          {product.conversionRate}%
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          Conversion Rate
                        </Typography>
                      </Box>
                      <CircularProgress value={product.conversionRate} />
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <Box p={2} display="flex" justifyContent="flex-end">
        <Button
          component={RouterLink}
          size="small"
          to="#"
          endIcon={<NavigateNextIcon />}
        >
          See all
        </Button>
      </Box>
    </Card>
  );
};

MostProfitableProducts.propTypes = {
  className: PropTypes.string
};

export default MostProfitableProducts;
